import React, {
    useEffect
} from "react"
import ReactDOMServer from "react-dom/server"
import ReactMDE from "react-simplemde-editor"

import MarkdownViewer from "./MarkdownViewer.js"

const MARKDOWN_EDITOR_MAX_HEIGHT_PX = 5000

const MarkdownEditor = props => {
    const handleGetMdeInstance = (instance) => {
/*       instance.toggleSideBySide()
      instance.toggleSideBySide() */
    }

    return (
      <ReactMDE
        {...props}
        options={{
          minHeight: "200px",
          maxHeight: "400px",
          minWidth:"800px",
          maxWidth:"1000px",
          previewRender: (text) =>
            ReactDOMServer.renderToStaticMarkup(
              <MarkdownViewer source={text} />
            )
        }}
        getMdeInstance={handleGetMdeInstance}
      />
    );
}

export default MarkdownEditor