import './App.css';
import "./index.css";
import {Route} from "react-router-dom"

import Navbar from "./components/Navbar"
import Home from "./views/Home"
import Learn from "./views/Learn"
import Practice from "./views/Practice"
import CreateArticle from "./views/CreateArticle.js"
import CreateExercise from "./views/CreateExercise.js"

function App() {
  return (
    <div className="overflow-x-hidden h-screen">
      <Navbar></Navbar>
      <Route path="/" exact component={Home}></Route>
      <Route path="/learn/:id" exact component={Learn}></Route>
      <Route path="/practice/:id" exact component={Practice}></Route>
      <Route path="/create-article" component={CreateArticle}></Route>
      <Route path="/create-exercise" component={CreateExercise}></Route>
      
    </div>
  );
}

export default App;
