import React, { Component, useState } from "react"
import axios from "axios"
import ArticleDropdown from "../components/ArticleDropdown"
import Dropdown from "../components/ArticleDropdown"
import CreateExerciseComponent from "../components/createExerciseComponent"

class CreateArticle extends Component{

    componentWillMount(){
        axios.get(`/api/create-exercise`)
    .then(response=>{
        let topics = []
        response.data.map(res =>{
            topics.push(res.topic)
        })
        let contents = []
        response.data.map(res =>{
            topics.push(res.content)
        })
        
        this.setState(
            {data:topics,
            contentData: response}
        )
        if(!response.data){
            this.props.history.push('/')
        }

    }).catch(()=>{
        if(this.state!=null) return

        this.props.history.push('/')
    })
    }
    render(){
        if(this.state == null) return null
        return(
            
            <>
            <div className="bg-gray-900 w-full h-full flex justify-between">

                
                    <div className="w-full bg-gray-900">
                        <CreateExerciseComponent topics={this.state.data} fullData={this.state.contentData} ></CreateExerciseComponent>
                    </div>
                
            </div>
            </>
            
        )
    }
}
export default CreateArticle