import React, { Component, useState } from "react"
import axios from "axios"
import MarkdownEditor from "../components/MarkdownEditor.js"
import MarkdownViewer from "../components/MarkdownViewer.js"
import ArticleDropdown from "../components/ArticleDropdown"
import CreateArticleComponent from "../components/createArticleComponent"

class CreateArticle extends Component{

    componentWillMount(){
        axios.get(`/api/create-article`)
    .then(response=>{
        let topics = []
        response.data.map(res =>{
            topics.push(res.name)
        })
        let contents = []
        response.data.map(res =>{
            topics.push(res.tasks)
        })
        
        this.setState(
            {data:topics,
            contentData: response}
        )
        if(!response.data){
            this.props.history.push('/')
        }

    }).catch(()=>{
        if(this.state!=null) return

        this.props.history.push('/')
    })
    }
    render(){
        if(this.state == null) return null
        return(
            <CreateArticleComponent topics={this.state.data} fullData={this.state.contentData}></CreateArticleComponent>
        )
    }
}
export default CreateArticle