import { Link } from "react-router-dom"

const Dropdown = (props) =>{
    return(
        <div className="group py-4 antialiased lg:w-full text-lg my-2">
            <p className="px-3 py-2 font-medium text-gray-800 bg-white rounded-t-lg text-center 
            cursor-pointer text-xl">{props.Category}</p>
            <div className="overflow-hidden bg-gray-800 font-medium h-auto text-white rounded-b-lg shadow-lg">


                {props.topics.map((topic, i) =>
                    (
                    <div className="flex justify-between" key={i}>
                    <div className="px-4 py-3">{topic}</div>
                    <div className="flex items-center">
                    <Link to={"learn/"+ topic.replace(/\s/g, '%20')}>
                    <svg className="w-9 h-9 px-1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10.496 2.132a1 1 0 00-.992 0l-7 4A1 1 0 003 8v7a1 1 0 100 2h14a1 1 0 100-2V8a1 1 0 00.496-1.868l-7-4zM6 9a1 1 0 00-1 1v3a1 1 0 102 0v-3a1 1 0 00-1-1zm3 1a1 1 0 012 0v3a1 1 0 11-2 0v-3zm5-1a1 1 0 00-1 1v3a1 1 0 102 0v-3a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                    </Link>
                    <Link to={"practice/"+ topic.replace(/\s/g, '%20')}>
                    <svg className="w-10 h-10 px-1 pr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
  <path d="M10.394 2.08a1 1 0 00-.788 0l-7 3a1 1 0 000 1.84L5.25 8.051a.999.999 0 01.356-.257l4-1.714a1 1 0 11.788 1.838L7.667 9.088l1.94.831a1 1 0 00.787 0l7-3a1 1 0 000-1.838l-7-3zM3.31 9.397L5 10.12v4.102a8.969 8.969 0 00-1.05-.174 1 1 0 01-.89-.89 11.115 11.115 0 01.25-3.762zM9.3 16.573A9.026 9.026 0 007 14.935v-3.957l1.818.78a3 3 0 002.364 0l5.508-2.361a11.026 11.026 0 01.25 3.762 1 1 0 01-.89.89 8.968 8.968 0 00-5.35 2.524 1 1 0 01-1.4 0zM6 18a1 1 0 001-1v-2.065a8.935 8.935 0 00-2-.712V17a1 1 0 001 1z" />
</svg>
</Link>
                    </div>
                </div>

                    )
                )}
                
            </div>
        </div>
    )
}

export default Dropdown