import { useState } from "react"


const ArticleDropDown = (props) =>{

 /*  let topics = []
  props.data.map(res =>{
            topics.push(res.topic)
        })
     */



  let [topicState, setTopicState] = useState("none")
  let firstPartTopics = props.topics.slice(0,Math.floor(props.topics.length)/2)
  let sendondPartTopics = props.topics.slice(Math.floor(props.topics.length)/2, props.topics.length)



  return(
    <>
    <div className="py-5 pt-10">

<div className="dropdown inline-block relative w.">
  <button className="bg-gray-300 text-gray-700 font-semibold py-2 px-4 rounded inline-flex items-center">
    <span className="mr-1">Select Topic</span>
    <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/> </svg>
  </button>
  <ul className="dropdown-menu absolute hidden text-gray-700 pt-1">
    {firstPartTopics.map( topic =>{
      return(
        <li onClick={() => props.onClickEvent(topic)} value={topic} key={topic} className=""><a className="rounded-t bg-gray-200 hover:bg-gray-400 py-2 px-4 block whitespace-no-wrap"
     href="#">{topic}</a></li>
      )
    })}

    
  </ul>
  <ul className="dropdown-menu absolute hidden mx-32 text-gray-700 pt-1">
    {sendondPartTopics.map( topic =>{
      return(
        <li onClick={() => props.onClickEvent(topic)}
        value={topic}
          key={topic} className=""><a className="rounded-t bg-gray-200 hover:bg-gray-400 py-2 px-4 block whitespace-no-wrap"
     href="#">{topic}</a></li>
      )
    })}

    
  </ul>
</div>

</div>
    </>
  )
}

export default ArticleDropDown