import { Link } from "react-router-dom"
const Navbar = () =>{
    return(
        <div className="bg-white w-screen h-20 flex justify-between text-2xl 
        text-gray-900 font-mono">
            <div className="px-20 flex items-center">
                <Link to="/">
                <p className="text-3xl cursor-pointer">Matheory</p>
                </Link>
            </div>
            <div className="flex items-center">
            <Link to="/create-article">
                <p className="px-7 cursor-pointer">Articles</p>
                </Link><Link to="/create-exercise">
                <p className="px-7 cursor-pointer">Exercise</p>
                </Link>
                <Link to="/learn">
                <p className="px-7 cursor-pointer">Learn</p>
                </Link>
                <Link to="/practice">
                <p className="px-7 pr-20 cursor-pointer">Practice</p>
                </Link>
                
            </div>
        </div>
    )
}
export default Navbar