import 'katex/dist/katex.min.css';
import {/*  InlineMath, */ BlockMath } from 'react-katex';
import React, {useEffect, useState, Component} from "react"
import axios from "axios"
import MarkdownViewer from "../components/MarkdownViewer"

class Learn extends Component{

    componentWillMount(){
        axios.get(`/api/learn/${this.props.match.params.id}`)
    .then(response=>{

        if(!response){
            this.props.history.push('/')
        }
        this.setState({content:response.data[0].content})


    }).catch((ex)=>{
        console.log(ex)
        this.props.history.push('/')
    })

    }

    render(){

        return(
            this.state?

            <div className="bg-gray-900 w-screen h-screen font-mono text-3xl text-white 
            flex flex-col">
                <div className="m-7">
                <MarkdownViewer source={this.state.content}></MarkdownViewer></div>
            </div>:<div className="bg-gray-900"></div>
        )
    }
}
export default Learn