import React, { useState } from "react"
import axios from "axios"
import MarkdownEditor from "../components/MarkdownEditor.js"
import MarkdownViewer from "../components/MarkdownViewer.js"
import ExerciseDropdown from "../components/ExerciseDropdown"

const CreateExcerciseComponent = (props) => {
    const [content, setContent] = useState([])
    const [data, setData] = useState(props.topics)
    const [topic, setTopic] = useState("None")
    const [exerciseData, setExerciseData] = useState()

    console.log(props.fullData.data)
    let topics = []
    props.fullData.data.map(res =>{
            topics.push(res.name)
        })

    const listLength = 100;

    const items = []
    for(let i = 0; i<listLength; i++){
        items.push({exercise:"", solution:""})
    }


    const handleDropdownClick = (callBackTopic) =>{
        setTopic(callBackTopic)
        
        var contentObj = props.fullData.data.find(obj =>{
            return obj.name == callBackTopic
        })

        contentObj.tasks.forEach( (obj,i) =>{
            items[i] = obj
        })
        
        setContent(items)


    }

    const handleSubmit = () => {
        axios.post("/api/exercise", {
            topic, content
        })
        console.log(content)
        

    }
    const handleDataChange = (event) =>{
        console.log(content)
        let [type, index] = event.target.placeholder.split(" ")
        index = index - 1

        if (!content[index]) {
            return
        }

        if(type == "solution") content[index].solution = event.target.value
        else content[index].exercise = event.target.value
        setContent([...content])
    }

    return (
        
        <div className="bg-gray-900 h-full flex justify-between">
            <div className=" w-3/5 h-4/5 m-10 overflow-auto rounded px-3 py-2">

              {items.map( (items,i) =>{  
                  return(
                <div className="flex flex-row my-2 h-1/9 border-gray-900 border-2 rounded-2xl bg-white" key={i}>

                        <div className="w-full h-full flex flex-row">
                            <div className="text-3xl ml-3 font items-center flex mr-10">{i+1}</div>
                        
                            <div className="text-xl ml-3 w-2/5 mx-4 font">
                                <textarea
                                    onChange={handleDataChange}
                                    className="w-full h-full px-2 border-gray-100 resize-none items-center flex"
                                    value={content[i] ? content[i].exercise : ""}
                                    key={"task " + i}
                                    placeholder={"task " + (i + 1)}
                                ></textarea>
                            </div>
                            <div className="text-xl ml-3 w-2/5 mx-4 font">
                                <textarea
                                    onChange={handleDataChange}
                                    className="w-full h-full border-gray-100 resize-none items-center flex"
                                    value={content[i] ? content[i].solution : ""}
                                    key={"solution" + i}
                                    placeholder={"solution " + (i + 1)}
                                ></textarea>
                            </div>
                        </div>
                    
                    

                </div>)
              })}
                

            </div>

            <div className="flex flex-col items-center w-2/5">
                
                <div>
                    <p className="text-white mt-4 text-xl">Selected Topic: {topic}</p>
                </div>
            
            <ExerciseDropdown topics={topics} onClickEvent={handleDropdownClick} ></ExerciseDropdown>

            <button className="bg-gray-300 text-black font-bold py-2 px-3 rounded-md m-3"
            onClick={handleSubmit}>Submit</button>
            </div>



        </div>
    )
}

export default CreateExcerciseComponent