import {/*  InlineMath, */ BlockMath } from 'react-katex';
import React, {useEffect, useState, Component} from "react"
import axios from "axios"


class Practice extends Component{
    
    componentWillMount(){
        axios.get(`/api/practice/${this.props.match.params.id}`)
    .then(response=>{

        let ResData = response.data[0]
        if(!ResData){
            this.props.history.push('/')
        }
        this.setState({
            task:{
            taskNr:1,
            tasks:ResData.tasks,
            maxTaskNr:ResData.tasks.length,
            solutionHide:true},
            data:{
                tasks: ResData.tasks,
                name: ResData.name
            }
        })
        
        let data = ResData

        
        
    }).catch(()=>{
        this.props.history.push('/')
    })

    }
    render(){
        if(this.state == null){
            return null
        }
        return(
        <div className="bg-gray-900 w-screen h-screen font-mono text-3xl text-gray-900 
        flex flex-col justify-evenly items-center">
    
    <div className="h-1/12 w-1/2 flex justify-evenly rounded-lg">
            <div>
            <svg onClick={ this.state.task.taskNr > 1 ?
       () => this.setState(prevState =>({
            task:{
                ...prevState.task,
                taskNr:prevState.task.taskNr-1,
                solutionHide: true
            },
            data:{
                ...prevState.data
            }
        })):null
    } className="w-24 h-24 p-3 cursor-pointer" xmlns="http://www.w3.org/2000/svg" fill="white" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 15l-3-3m0 0l3-3m-3 3h8M3 12a9 9 0 1118 0 9 9 0 01-18 0z" />
    </svg>
            </div>
            <div className="align-middle items-center flex text-white">
            {this.state.data.name}, {"No." + this.state.task.taskNr}
            </div>
            <div>
            <svg className="w-24 h-24 p-3 cursor-pointer" xmlns="http://www.w3.org/2000/svg" fill="white" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" onClick={
          this.state.task.taskNr < this.state.task.maxTaskNr ?
       () => this.setState(prevState => ({
            task:{
                ...prevState.task,
                taskNr:prevState.task.taskNr+1,
                solutionHide: true
            },
            data:{
                ...prevState.data
            }
        })):null

    } strokeLinejoin="round" strokeWidth={2} d="M13 9l3 3m0 0l-3 3m3-3H8m13 0a9 9 0 11-18 0 9 9 0 0118 0z" />
    </svg>
            </div>
            </div>
            <div className="flex flex-col justify-evenly items-center h-3/4 w-full">
    
            <div className="h-1/6 w-auto lg:p-20 p-5 bg-white flex justify-center rounded-lg mb-10 items-center lg:text-3xl md:text-2xl text-xl">
            <BlockMath math={this.state.task.tasks[this.state.task.taskNr-1].exercise}/>
            </div>
    
            <div className="h-1/6 w-auto lg:p-20 p-5 bg-white flex justify-center rounded-lg mb-10 select-none" onClick={
            () => this.setState(prevState =>({
                task:{
                    ...prevState.task,
                    solutionHide:!prevState.task.solutionHide
                },
                data:{
                    ...prevState.data
                }
            }))}>
            <p className="text-gray-900 lg:text-7xl md:text-5xl text-lg flex items-center cursor-pointer">{this.state.task.solutionHide ? 
            <div>
                <svg className="w-14 h-14" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="black">
  <path d="M11 3a1 1 0 10-2 0v1a1 1 0 102 0V3zM15.657 5.757a1 1 0 00-1.414-1.414l-.707.707a1 1 0 001.414 1.414l.707-.707zM18 10a1 1 0 01-1 1h-1a1 1 0 110-2h1a1 1 0 011 1zM5.05 6.464A1 1 0 106.464 5.05l-.707-.707a1 1 0 00-1.414 1.414l.707.707zM5 10a1 1 0 01-1 1H3a1 1 0 110-2h1a1 1 0 011 1zM8 16v-1h4v1a2 2 0 11-4 0zM12 14c.015-.34.208-.646.477-.859a4 4 0 10-4.954 0c.27.213.462.519.476.859h4.002z" />
</svg>
            </div>: <BlockMath math={this.state.task.tasks[this.state.task.taskNr-1].solution}/> }</p>
            </div>
            </div>
    
        </div>
        
        )

    }
}

export default Practice